'use client'
import { useState, useRef, useCallback } from 'react'

interface ClothingCustomizerProps {
  category: string
}

export default function ClothingCustomizer({ category }: ClothingCustomizerProps) {
  const [customImage, setCustomImage] = useState<string | null>(null)
  const [dragActive, setDragActive] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)

  const processImageFile = useCallback((file: File) => {
    if (file && file.type.startsWith('image/')) {
      const reader = new FileReader()
      reader.onload = () => {
        setCustomImage(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }, [])

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      processImageFile(file)
    }
  }

  const handlePaste = (e: React.ClipboardEvent<HTMLDivElement>) => {
    const items = e.clipboardData.items
    for (const item of items) {
      if (item.type.startsWith('image/')) {
        const file = item.getAsFile()
        if (file) {
          processImageFile(file)
        }
        break
      }
    }
  }

  const handleDrag = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    if (e.type === 'dragenter' || e.type === 'dragover') {
      setDragActive(true)
    } else if (e.type === 'dragleave') {
      setDragActive(false)
    }
  }

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    setDragActive(false)
    
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      processImageFile(e.dataTransfer.files[0])
    }
  }

  const clearImage = () => {
    setCustomImage(null)
    if (fileInputRef.current) {
      fileInputRef.current.value = ''
    }
  }

  const getClothingIcon = (category: string) => {
    const icons: { [key: string]: string } = {
      'Camisas': 'üëî',
      'Chompas': 'üß•',
      'Chamarras': 'üß•',
      'Busos': 'üëï',
      'Pantalones': 'üëñ',
      'Poleras': 'üëï'
    }
    return icons[category] || 'üëï'
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
      {/* Clothing Preview Section */}
      <div className="bg-gray-50 rounded-2xl p-6">
        <h3 className="text-lg font-semibold text-gray-800 mb-4 text-center">
          Vista Previa - {category}
        </h3>
        
        <div className="relative bg-white rounded-xl p-8 min-h-[400px] flex items-center justify-center border-2 border-gray-200">
          {/* Base clothing item */}
          <div className="relative">
            <div className="text-8xl mb-4 text-center">
              {getClothingIcon(category)}
            </div>
            <div className="text-center text-gray-600 font-medium">
              {category}
            </div>
            
            {/* Custom image overlay */}
            {customImage && (
              <div className="absolute inset-0 flex items-center justify-center">
                <img
                  src={customImage}
                  alt="Dise√±o personalizado"
                  className="max-w-full max-h-full object-contain rounded-lg opacity-80 mix-blend-multiply"
                />
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Upload Section */}
      <div className="space-y-6">
        <h3 className="text-lg font-semibold text-gray-800">
          Personaliza tu {category}
        </h3>

        {/* Upload Area */}
        <div
          ref={containerRef}
          onPaste={handlePaste}
          onDragEnter={handleDrag}
          onDragLeave={handleDrag}
          onDragOver={handleDrag}
          onDrop={handleDrop}
          className={`border-2 border-dashed rounded-2xl p-8 text-center transition-all duration-200 cursor-pointer ${
            dragActive
              ? 'border-blue-500 bg-blue-50'
              : 'border-gray-300 hover:border-gray-400 hover:bg-gray-50'
          }`}
          tabIndex={0}
        >
          <div className="space-y-4">
            <div className="text-4xl">üìÅ</div>
            <div>
              <p className="text-lg font-medium text-gray-700 mb-2">
                Sube tu dise√±o
              </p>
              <p className="text-sm text-gray-500 mb-4">
                Arrastra y suelta, pega desde el portapapeles, o haz clic para seleccionar
              </p>
              
              <input
                type="file"
                ref={fileInputRef}
                accept="image/*"
                onChange={handleFileChange}
                className="hidden"
              />
              
              <button
                onClick={() => fileInputRef.current?.click()}
                className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition duration-200"
              >
                Seleccionar Archivo
              </button>
            </div>
          </div>
        </div>

        {/* Instructions */}
        <div className="bg-blue-50 rounded-lg p-4">
          <h4 className="font-medium text-blue-900 mb-2">Instrucciones:</h4>
          <ul className="text-sm text-blue-800 space-y-1">
            <li>‚Ä¢ Arrastra una imagen desde tu computadora</li>
            <li>‚Ä¢ Copia una imagen y p√©gala con Ctrl+V</li>
            <li>‚Ä¢ Haz clic en "Seleccionar Archivo" para buscar</li>
            <li>‚Ä¢ Formatos soportados: JPG, PNG, GIF</li>
          </ul>
        </div>

        {/* Current Image Info */}
        {customImage && (
          <div className="bg-green-50 rounded-lg p-4">
            <div className="flex items-center justify-between">
              <div>
                <h4 className="font-medium text-green-900">Imagen cargada</h4>
                <p className="text-sm text-green-700">Tu dise√±o se ha aplicado correctamente</p>
              </div>
              <button
                onClick={clearImage}
                className="bg-red-500 text-white px-4 py-2 rounded-lg hover:bg-red-600 transition duration-200 text-sm"
              >
                Eliminar
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
